



/* Container needed to position the overlay. Adjust the width as needed */
.container {
  position: relative;
  width: 100%;
  max-width: 300px;
}

/* Make the image to responsive */
.hero-image {
  display: block;
  width: 100%;
  height: auto;
}


/* Centered text */
#centered-about {
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    background: rgb(255, 255, 255, .3);
}
   
  .portrait {
   
    height: auto;
    padding: 15px;
    border-radius: 50%;
  }

  p {
      font-size: 18px;
      margin: 15px;
  }

 #connect{
      margin:15px;

  }
  
  @media screen and (max-width: 700px) {
/* Centered text */
#centered-about {
    position: absolute;
    top: 70%; 
    left: 50%;
    
    text-align: center;
    background: rgb(255, 255, 255, .3);
  }
    .sidebar a {float: left;}
    div.content {margin-left: 0;}
    
    .portrait {
        display: none;
       
    }
  }